@import "../Variables.scss";

.ToastsContainer {
	position: fixed;
	top: 0; left: 0; bottom: 0; right: 0;
	margin-bottom: 50px;

	pointer-events: none;
	z-index: 10000;

	display: flex;
	flex-direction: column;
	
	justify-content: flex-end;
	align-items: center;

	.Toast {
		background-color: change-color($primaryColor, $saturation: 60%);

		margin: 5px 0;
		padding: 10px;
		border-radius: 5px;

		&.Success { background-color: change-color(#00ff2a, $saturation: 60%); }
		&.Warning { background-color: change-color(#ffd900, $saturation: 60%); }
		&.Failure { background-color: change-color(#ff0000, $saturation: 60%); }

		&.Active { animation: toast-open 500ms ease-out 0ms 1; }
		&.Closing { animation: toast-close 500ms ease-out 0ms 1; }

		@keyframes toast-open {
			0% {
				opacity: 0;
				transform: translateX(-200%);
			}

			100% {
				opacity: 1;
				transform: translateX(0);
			}
		}

		@keyframes toast-close {
			0% {
				opacity: 1;
				transform: translateX(0);
			}

			100% {
				opacity: 0;
				transform: translateX(200%);
			}
		}
	}
}