@import "./Variables.scss";

html {
	background-color: $primaryBG;
	color: white;

	-moz-user-select: none;
	-webkit-user-select: none;
	overflow-x: hidden;
}

#root {
	overflow-x: hidden;

	.Spinner {
		@keyframes spinner {
			0% { transform: rotateZ(0deg) }
			100% { transform: rotateZ(360deg) }
		}

		animation-name: spinner;
		animation-duration: 1s;
		animation-timing-function: linear;
		animation-iteration-count: infinite;
	}

	.Dropdown {
		width: 200px;

		position: absolute;
		top: 0; left: -210px; bottom: -1px;
	}

	.Mobile {
		.ClipboardModal {
			height: 55px;
		}

		.Header {
			overflow: hidden;
			height: auto;
			max-height: 70px;

			position: absolute;
			top: 0; left: 0; right: 0;

			z-index: 100;
			transition: max-height $t;
		
			.HeaderButtons {
				pointer-events: none;

				.Container {
					display: block;
					pointer-events: all;
				}

				.Button {
					background: transparent;
					width: 250px;

					pointer-events: all;

					.Content {
						color: white;
						width: 250px;
					}

					.Icon {
						transform: none;
						filter: invert(100%);
					}
				}

				.RouteButton .Icon { transition: transform $t; }
			}

			.HeaderMain {
				font-size: 7vw;
				line-height: 45px;

				margin-top: 5px;
			}

			&.Active {
				max-height: 60vh;

				.RouteButton .Icon { transform: rotateZ(-90deg); }
			}
		}
	}

	a {
		color: $primaryColor;

		&:visited {
			color: lighten($primaryColor, 30);
		}
	}
}

.Header {
	background-color: $secondaryBG;

	position: relative;
	height: 70px;

	.HeaderMain {
		font-size: 2.5rem;
		font-weight: bolder;

		position: absolute;
		right: 20px;
		top: 5px;
	}
}

.Main {
	min-height: 100vh;
}

.Footer {
	background-color: $secondaryBG;
	height: 50px;

	position: relative;

	span {
		position: absolute;
		top: 50%; left: 50%;
		transform: translate(-50%, -50%);
	}
}

.Mobile {
	.Footer {
		span {
			width: 100%;
			left: 0;
			transform: translateY(-50%);
			text-align: center;

			font-size: 0.6rem;
		}
	}
}