@import "../Variables.scss";

.MainPage {
	position: relative;
	min-height: 100vh;

	.Background {
		z-index: -5;
		position: absolute;
		top: 0; left: 0; bottom: 0; right: 0;

		background-position: 100% 100%;
		background-size: 100%;
		background-repeat: no-repeat;

		filter: blur(5px);

		.Overlay {
			position: absolute;
			top: 0; left: 0; bottom: 0; right: 0;

			background: linear-gradient(to right, $primaryBG 30%, transparent);
		}
	}

	.Head {
		color: white;
		opacity: 0.9;

		text-align: right;
		margin-right: 100px;

		position: absolute;
		top: 30%; right: 0;

		.Title {
			font-weight: bolder;
			font-size: 4.5rem;

			animation: init 1s normal 1s forwards;
			opacity: 0;
		}
	
		.Description {
			font-weight: lighter;
			font-size: 3rem;

			animation: init 1s normal 2s forwards;
			opacity: 0;
		}

		@keyframes init {
			0% {
				opacity: 0;
				transform: translateX(500px);
			}
			100% {
				opacity: 1;
				transform: none;
			}
		}
	}
}

.Mobile .MainPage {
	.Background {
		background-size: 200% 100%;
	}

	.Head {
		top: 20%;
		margin-right: 30px;

		.Title {
			font-size: 3rem;
		}

		.Description {
			font-size: 2rem;
		}
	}
}