@import "../Variables.scss";

.CommsPage {
	.TabsContainer {
		position: relative;
		top: -56px;
	}

	.Status {
		text-align: center;
		font-size: 3rem;
		font-weight: bolder;

		&.Closed { color: #e65a19; }
		&.Opened { color: #19e686; }

		&.Desc {
			color: #dbdbdb;
			font-size: 2rem;
			font-weight: normal;
			font-style: italic;
		}
	}

	.CommissionItem {
		position: relative;
		margin-top: 50px;

		div { text-align: center; }

		.Title {
			font-size: 1.5rem;
			font-weight: bold;
		}

		.Description {
			color: #a3a3a3;

			.Price { color: #19e686; }
			.Number { color: #e65a19; }
			.Group { opacity: 0.8; }
		}

		.Images {
			position: relative;
			left: 50%;
			transform: translateX(-50%);

			img {
				cursor: pointer;
	
				border-radius: 10px;
				max-width: 90vw;
				margin: 20px;
			}
		}
	}
}